<template>
  <div class="tag">
    <button
      class="tag__btn"
      @click="
        currentTag = tag;
        filteredArticleIds();
      "
      v-for="tag in tagArray"
      :key="tag.id"
      :class="{ selected: currentTag === tag }"
    >
      {{ tag }}
    </button>
  </div>
</template>

<script>
export default {
  name: "SiteTag",
  props: {
    tags: Array,
  },

  data() {
    return {
      tagArray: [],
      currentTag: "",
    };
  },
  mounted() {
    this.tagArray = new Set(this.tags.map((article) => article.tag));
    this.tagArray = Array.from(
      new Set(this.tags.map((article) => article.tag))
    );
  },

  methods: {
    filteredArticleIds: function () {
      let filteredIds = [];
      if (!this.currentTag) {
        filteredIds = this.tags.map((article) => article.id);
      } else {
        filteredIds = this.tags
          .filter((article) => article.tag === this.currentTag)
          .map((article) => article.id);
      }
      this.$emit("filtered-article-ids", filteredIds);
      return filteredIds;
    },
  },
};
</script>

<style lang="scss" scoped>
.tag {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  &__btn {
    padding: 9px 30px;
    border-radius: 10px;
    background: #f4f0ec;
    border: none;
    color: #292f36;

    text-align: center;
    font-family: Jost;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 125%;
    letter-spacing: 0.36px;
  }
}
.selected {
  background: #292f36;
  color: #fff;
}
</style>
