      :class="linkClass"
      >{{ item.text }}</router-link
    >
  </nav>
</template>

<script>
export default {
  name: "SiteMenu",
  props: {
    menuClass: String,
    linkClass: String,
  },

  data() {
    return {
      menuItems: [
        { href: "/", text: "Home" },
        { href: "/blog", text: "Blog" },
        { href: "/blogDetails", text: "Details" },
      ],
    };
  },
};
</script>

