<template>
  <a :class="buttonClasses"
    ><svg
      xmlns="http://www.w3.org/2000/svg"
      width="52"
      height="53"
      viewBox="0 0 52 53"
      fill="none"
    >
      <circle cx="26" cy="26.267" r="26" fill="#F4F0EC" />
      <path
        d="M23.7714 32.9527L29.7143 26.267L23.7714 19.5813"
        stroke="#292F36"
        stroke-width="2"
        stroke-linecap="round"
        stroke-linejoin="round"
      />
    </svg>
  </a>
</template>

<script>
export default {
  name: "CircleButton",
  props: {
    classCircleButton: String,
    isSecondArticle: Boolean,
  },

  data() {
    return {};
  },
  computed: {
    buttonClasses() {
      return [
        this.classCircleButton,
        this.isSecondArticle ? "second-article-button" : "",
      ];
    },
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/styles/variables.scss";
@import "../assets/styles/mixins.scss";
a {
  border-radius: $circle-border-radius;
  border-color: transparent;
  margin-left: auto;
  transition: transform $transition;
  cursor: pointer;
  &:hover svg {
    transform: scale($scale-factor-large);
  }

  &:hover svg circle {
    fill: $buttonTextColor;
  }

  // &:nth-child(2) svg circle{
  //   fill: $buttonTextColor;
  // }

  &.second-article-button svg circle {
    fill: $buttonTextColor;
  }
}
</style>
