<template>
  <div class="pagination">
    <button
      class="pagination__btn"
      :class="{
        'pagination__btn--selected':
          pageNumber === currentPage ||
          (pageNumber === totalPages && currentPage > 3),
      }"
      v-for="pageNumber in limitedPages"
      :key="pageNumber"
      @click="changePage(pageNumber)"
    >
      0{{ pageNumber }}
    </button>
    <button v-if="totalPages > 3 && currentPage <= 3" class="pagination__btn">
      ...
    </button>
    <button
      v-if="totalPages > 3 && currentPage > 3 && currentPage < totalPages"
      class="pagination__btn"
      :class="{ 'pagination__btn--selected': currentPage === currentPage }"
      @click="changePage(currentPage)"
    >
      {{ currentPage }}
    </button>
    <button
      v-if="totalPages > 3"
      class="pagination__btn"
      :class="{ 'pagination__btn--selected': currentPage === totalPages }"
      @click="changePage(totalPages)"
    >
      {{ totalPages }}
    </button>
    <button class="pagination__btn" @click="nextPage">
      <svg>
        <path
          d="M23.7714 32.9527L29.7143 26.267L23.7714 19.5813"
          stroke="#292F36"
          stroke-width="2"
          stroke-linecap="round"
          stroke-linejoin="round"
        />
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  name: "SitePagination",
  props: ["items"],

  data() {
    return {
      currentPage: 1,
      pageSize: 6,
    };
  },

  methods: {
    changePage: function (pageNumber) {
      this.currentPage = pageNumber;
      this.$emit("page-changed", this.paginatedData);
    },
    nextPage: function () {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
        this.$emit("page-changed", this.paginatedData);
      }
    },
  },
  computed: {
    totalPages: function () {
      return Math.ceil(this.items.length / this.pageSize);
    },
    paginatedData: function () {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.items.slice(start, end);
    },
    limitedPages: function () {
      return this.totalPages > 3 ? 3 : this.totalPages;
    },
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/styles/variables.scss";
@import "../assets/styles/mixins.scss";
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding-top: 51px;
}

.pagination__btn {
  width: 52px;
  height: 53px;
  border: 1px solid $accentColor;
  border-radius: 50%;
  background-color: #fff;
  position: relative;
  @include text-styles(
    $colorText,
    1rem,
    $line-height-medium,
    $letter-spacing-small
  );
  font-style: normal;
  font-weight: 600;
  text-transform: capitalize;
  padding: 14px 17px;
  transition: background-color $transition;
}

.pagination__btn:last-child {
  padding: 0;
}
.pagination__btn:hover {
  background-color: $infoBlockColor;
  border-color: transparent;
}

.pagination__btn svg {
  width: 50px;
  height: 51.3px;
  fill: $backgroundSiteColor;
}
.pagination__btn svg circle {
  fill: white;
  border: none;
}

.pagination__btn:hover svg circle {
  fill: $infoBlockColor;
  stroke: none;
  border-color: transparent;
}
.pagination__btn--selected {
  /*       */
  background-color: $infoBlockColor;
  border-color: transparent;
}
</style>
