<template>
  <div class="product">
    <router-link :to="'/productDetails'">
      <ProductImage
        v-for="(content, index) in product.contents"
        :img-class="'product__img'"
        :key="index"
        :is="content.type"
        v-bind="getProps(content.type, content.data)"
      />

      <div class="product__content">
        <div class="product__content-desc">
          <h3 class="product__content-title">{{ product.categoryText }}</h3>
          <SiteBreadCrumbs :items="crumbsProducts" />
        </div>

        <CircleButtonProduct
          :button-link="product.link"
          :class-circle-button="'product__button'"
        />
      </div>
    </router-link>
  </div>
</template>

<script>
import CircleButtonProduct from "./CircleButtonProduct.vue";
import ProductImage from "./ProductImage.vue";
import SiteBreadCrumbs from "./SiteBreadCrumbs.vue";

export default {
  name: "SiteProduct",
  props: {
    product: {
      type: Object,
      required: true,
    },
    imgClass: {
      type: String,
    },
  },
  data() {
    return {
      crumbsProducts: [
        { name: "Decor", route: "/ProductsDetails/decor" },
        { name: "Artchitecture", route: "/ProductsDetails/Artchitecture" },
      ],
    };
  },
  methods: {
    getProps(type, data) {
      if (type === "ProductImage") {
        return { ...data, imgClass: this.imgClass };
      }
      return data;
    },
  },
  components: { CircleButtonProduct, ProductImage, SiteBreadCrumbs },
};
</script>

<style lang="scss" scoped>
@import "../assets/styles/variables.scss";
@import "../assets/styles/mixins.scss";
.product {
  max-width: 45.6%;
  &__button {
    border-radius: $circle-border-radius;
    border-color: transparent;
    margin-left: auto;
    transition: transform $transition;
    cursor: $cursor-link;
  }
  &__button:hover svg {
    transform: scale($scale-factor-large);
  }
  &__content {
    @include flex-center();
  }
  &__content-title {
    @include text-styles(
      $colorHeading,
      $product-title-text-size,
      $line-height-small,
      $letter-spacing-extra-medium
    );
    font-family: $textHeading;
    font-weight: $font-weight-regular;
  }
  &__content-text {
    @include text-styles(
      $colorText,
      $font-size-text,
      $line-height-medium,
      $letter-spacing-small
    );
    font-weight: $font-weight-regular;
  }
}
</style>
