<template>
  <div>
    <picture>
      <source
        v-for="source in sources"
        :key="source.id"
        :type="'image/' + source.type"
        :media="source.media"
        :srcset="source.srcset"
        :sizes="source.sizes"
      />
      <img
        :class="imgClass"
        :src="defaultImg"
        :alt="alt"
        style="width: 100%"
        loading="lazy"
      />
    </picture>
  </div>
</template>

<script>
export default {
  name: "ProductImage",
  props: {
    sources: {
      type: Array,
      required: true,
    },
    defaultImg: {
      type: String,
      required: true,
    },
    alt: {
      type: String,
      required: true,
    },
    imgClass: {
      type: String,
    },
  },

  data() {
    return {};
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/styles/variables.scss";
@import "../assets/styles/mixins.scss";
.product {
  &__img {
    padding-bottom: 28px;
  }
}
</style>
