<template>
  <div id="app">
    <BlockHeader />
    <router-view />
    <BlockFooter v-if="$route.path !== '/404'"/>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import BlockHeader from '@/blocks/BlockHeader.vue';
import BlockFooter from './blocks/BlockFooter.vue';



export default {
  name: "App",
  components: {
    BlockHeader,
    BlockFooter
},
  data() {
    return {
    };
  },
  created() {
    this.fetchArticles();
    this.fetchProducts();
  },
  methods: {
    ...mapActions( ['fetchArticles']),
    ...mapActions( ['fetchProducts'])
  },
  computed: {
    ...mapGetters( ['articles']),
    ...mapGetters( ['products'])
  },
};
</script>

