<template>
  <section class="articles-and-news" id="blog">
    <div class="container">
      <div class="articles-and-news__content articles-and-news__latest_title">
        <h2 class="articles-and-news__title">Articles &amp; News</h2>
      </div>
      <div class="articles-and-news__articles">
        <SiteArticle
          v-for="article in paginatedArticles.slice(-6)"
          :key="article.id"
          :article="article"
          imgClass="article__img"
        />
      </div>
      <SitePagination :items="articles" @page-changed="onPageChanged" />
    </div>
  </section>
</template>

<script>
import { mapGetters } from 'vuex';
import SitePagination from "@/components/SitePagination.vue";
import SiteArticle from "@/components/SiteArticle.vue";

export default {
  name: "BlockAtricleAndNewsBlog",
  data() {
    return {
      paginatedArticles: [],
    };
  },
  methods: {
    onPageChanged(paginatedArticles) {
      this.paginatedArticles = paginatedArticles;
    },
  },
  components: { SitePagination, SiteArticle },
  
  computed: {
    ...mapGetters( ['articles'])
  },
  mounted() {
    this.paginatedArticles = this.articles;
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/styles/variables.scss";
@import "../assets/styles/mixins.scss";
.articles-and-news__latest_title {
    margin-bottom: 27px;
    text-align: start;
}
.articles-and-news {
  padding-top: $block-padding;
  padding-bottom: $block-padding;
  &__articles {
    display: flex;
    gap: $articles-gap;
    justify-content: center;
    width: 100%;
    flex-wrap: wrap;
  }

  &__title {
    @include text-styles(
      $colorHeading,
      $h2-size,
      $line-height-small,
      $letter-spacing-large
    );
    font-family: $textHeading;
    padding-bottom: 12px;
  }
}
</style>
