<template>
  <router-link
    :to="{ name: 'blogDetailsWithId', params: { id: latestArticle.id } }"
  >
    <article class="article article__latest">
      <div class="article__image-link article__latest_image-link">
        <ArticleImage
          v-for="(content, index) in latestArticle.contents"
          :key="index"
          :is="content.type"
          v-bind="getProps(content.type, content.data)"
        />
      </div>
      <div class="article__content article__latest_content">
        <h3 class="article__title">{{ latestArticle.title }}</h3>
        <ArticleText
          v-for="(content, index) in latestArticle.contents"
          :key="index"
          :is="content.type"
          v-bind="getDesc(content.type, content.data)"
        />
        <div class="article__some">
          <time class="article__time" :datetime="latestArticle.datetime">{{
            latestArticle.date
          }}</time>
          <CircleButton
            :button-link="latestArticle.link"
            :class-circle-button="'article__button'"
          />
        </div>
      </div>
    </article>
  </router-link>
</template>

<script>
import { mapGetters } from "vuex";
import CircleButton from "./CircleButton.vue";
import ArticleImage from "./ArticleImage.vue";
import ArticleText from "./ArticleText.vue";

export default {
  name: "LatestPost",
  props: {
    imgClass: {
      type: String,
    },
  },
  components: { CircleButton, ArticleImage, ArticleText },

  data() {
    return {};
  },

  mounted() {},

  methods: {
    getProps(type, data) {
      if (type === "ArticleImage") {
        return { ...data, imgClass: this.imgClass };
      }
      return null;
    },

    getDesc(type, data) {
      if (type === "ArticleText") {
        return { ...data, text: data.text.slice(0, 2) };
      }
      return null;
    },
  },
  computed: {
    ...mapGetters(["articles"]),
    latestArticle() {
      return this.articles[this.articles.length - 1];
    },
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/styles/variables.scss";
@import "../assets/styles/mixins.scss";
.article {
  border: 1px solid $highlightColor;
  padding: $article-padding;
  border-radius: $article-border-radius;
  width: 31.8%;
  @include flex-column();
  transition: background-color $transition, transform $transition;
  &_text {
    @include text-styles(
      $colorText,
      $font-size-text,
      $line-height-medium,
      $letter-spacing-small
    );
    padding-bottom: 40.5px;
  }
  &__image-link {
    position: relative;
    min-height: 289px;
  }

  &__img {
    margin-bottom: 22px;
    border-top-left-radius: 45px;
    border-top-right-radius: 45px;
  }

  &__some {
    @include flex-center();
    margin-bottom: 15px;
    margin-top: auto;
  }

  &__title {
    @include text-styles(
      $colorHeading,
      $product-title-text-size,
      $line-height-small,
      $letter-spacing-extra-medium
    );
    font-family: $textHeading;
    margin-bottom: 30px;
    max-width: 86%;
  }

  &__time {
    @include text-styles(
      $colorText,
      $article-link-text-size,
      $line-height-medium,
      $letter-spacing-extra-small
    );
    text-transform: capitalize;
  }

  &:nth-child(2) {
    background-color: $highlightColor;
  }

  &:hover {
    background-color: $highlightColor;
    transform: scale($scale-factor-small);
  }

  &_text {
    @include text-styles(
      $colorText,
      $font-size-text,
      $line-height-medium,
      $letter-spacing-small
    );
    padding-bottom: 40.5px;
  }
  &__button {
    &:nth-child(2) svg circle {
      fill: $buttonTextColor;
    }
  }
}
.article:hover .article__button svg circle {
  fill: $buttonTextColor;
}
.article__latest {
  width: 100%;
  display: flex;
  flex-direction: row;
  gap: 65px;
  &_content {
    @include flex-column();
    max-width: 39%;
    padding: 30px 0;
  }

  &_image-link {
    max-width: 48%;
  }
}
</style>
