import Vue from 'vue'
import App from './App.vue'
import Vuex from "vuex";
import store from './store/index.js'
import VueRouter from "vue-router";
import { router } from "@/router";

Vue.use(Vuex);
Vue.use(VueRouter);

Vue.config.productionTip = false

new Vue({
  render: h => h(App),
  store,
  router
}).$mount('#app')


