<template>
  <div>
    <picture>
      <source
        v-for="source in sources"
        :key="source.id"
        :type="'image/' + source.type"
        :media="source.media"
        :srcset="source.srcset"
        :sizes="source.sizes"
      />
      <img
        :class="imgClass"
        :src="defaultImg"
        :alt="alt"
        style="width: 100%"
        loading="lazy"
      />
    </picture>
  </div>
</template>

<script>
export default {
  name: "ArticleImage",
  props: {
    sources: {
      type: Array,
      required: true,
    },
    defaultImg: {
      type: String,
      required: true,
    },
    alt: {
      type: String,
      required: true,
    },
    imgClass: {
      type: String,
    },
  },

  data() {
    return {};
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/styles/variables.scss";
@import "../assets/styles/mixins.scss";
.article {
  &__img {
    margin-bottom: 22px;
    border-top-left-radius: 45px;
    border-top-right-radius: 45px;
    min-height: 287.2px;
  }
}
.article__full {
  &_img {
    border-radius: 50px;
    margin-bottom: 0;
    margin-bottom: 45px;
    max-height: 540px;
  }
}
.article__latest {
  &_img {
    border-radius: 50px;
    margin-bottom: 0;
  }
}
</style>
