<template>
  <div class="blog-details">
    <div class="container">
      <div class="blog-details__flex">
        <section class="articles-and-news details">
          <FullArticle
            v-for="article in displayArticle"
            :key="article.id"
            :article="article"
            imgClass="article__img article__full_img"
          />
        </section>
        <nav class="tags" v-if="!$route.params.id && !$route.params.tag">
          <p class="tags__heading">Tags</p>
          <SiteTag
            :tags="articles"
            @filtered-article-ids="onFilteredArticleIds"
          />
        </nav>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import FullArticle from "@/components/FullArticle.vue";
import SiteTag from "@/components/SiteTag.vue";

export default {
  name: "FullArticles",
  data() {
    return { filteredArticleIds: [] };
  },
  components: { FullArticle, SiteTag },
  computed: {
    ...mapGetters(["articles"]),
    displayArticle() {
      const id = this.$route.params.id;
      const tag = this.$route.params.tag;
      if (id) {
        const article = this.articles.find((article) => article.id === id);
        return article ? [article] : [];
      } else if (tag) {
        return this.articles.filter(article => article.tag.replace(/\s/g, '') === tag);
      } 
      return this.filteredArticleIds;
    },
  },
  methods: {
    onFilteredArticleIds(filteredIds) {
      this.filteredArticleIds = this.articles.filter((article) =>
        filteredIds.includes(article.id)
      );
    },
  },
  mounted() {
    this.filteredArticleIds = this.articles;
    this.$nextTick(() => {
      document.querySelector(".article__title").scrollIntoView();
    });
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/styles/variables.scss";
@import "../assets/styles/mixins.scss";
.blog-details {
  padding-top: 200px;
  padding-bottom: 96px;
  &__flex {
    display: flex;
    column-gap: 57px;
  }
}
.details {
  padding-top: 0;
  padding-bottom: 0;
  max-width: 66.5%;
}
.tags {
  max-width: 28%;
  margin-left: auto;
  &__heading {
    @include text-styles(
      $colorHeading,
      $footer-heading-size,
      $line-height-small,
      $letter-spacing-small
    );
    font-family: $textHeading;
    padding-bottom: 24px;
  }
}
</style>
