<template>
  <article class="article">
    <router-link
      :to="{ name: 'blogDetailsWithId', params: { id: article.id } }"
    >
      <div class="article__image-link">
        <ArticleImage
          v-for="(content, index) in article.contents"
          :key="index"
          :is="content.type"
          v-bind="getProps(content.type, content.data)"
        />
      </div>
      <h3 class="article__title">
        {{ article.title }}
      </h3>
      <div class="article__some">
        <time class="article__time" :datetime="article.date">{{
          article.date
        }}</time>
        <CircleButton :class-circle-button="'article__button'" />
      </div>
    </router-link>

    <div class="article__link-section">
      <router-link
        :to="{
          name: 'blogDetailsWithTag',
          params: { tag: article.tag.replace(/\s/g, '') },
        }"
      >
        <p class="article__link">{{ article.tag }}</p>
      </router-link>
    </div>
  </article>
</template>

<script>
import CircleButton from "./CircleButton.vue";
import ArticleImage from "./ArticleImage.vue";

export default {
  name: "SiteArticle",
  props: {
    article: {
      type: Object,
      required: true,
    },
    imgClass: {
      type: String,
    },
  },
  data() {
    return {};
  },
  methods: {
    getProps(type, data) {
      if (type === "ArticleImage") {
        return { ...data, imgClass: this.imgClass };
      }
      return data;
    },
  },

  components: { CircleButton, ArticleImage },
};
</script>

<style lang="scss" scoped>
@import "../assets/styles/variables.scss";
@import "../assets/styles/mixins.scss";
.article {
  border: 1px solid $highlightColor;
  padding: $article-padding;
  border-radius: $article-border-radius;
  width: 31.8%;
  @include flex-column();
  transition: background-color $transition, transform $transition;
  position: relative;

  &__image-link {
    position: relative;
    min-height: 289px;
  }

  &__link-section {
    border-radius: $article-link-section-border-radius;
    background: $backgroundSiteColor;
    position: absolute;
    left: 9%;
    bottom: 45%;
    padding: $article-link-section-padding;
    transition: transform $transition;
    &:hover {
      transform: scale($scale-factor-medium);
    }
  }
  &__link {
    @include text-styles(
      $colorText,
      $article-link-text-size,
      $line-height-medium,
      $letter-spacing-small
    );
    font-weight: $font-weight-regular;
    text-transform: capitalize;
  }

  &__some {
    @include flex-center();
    margin-bottom: 15px;
    margin-top: auto;
  }

  &__title {
    min-height: 94px;
    @include text-styles(
      $colorHeading,
      $product-title-text-size,
      $line-height-small,
      $letter-spacing-extra-medium
    );
    font-family: $textHeading;
    margin-bottom: 30px;
    max-width: 86%;
  }

  &__time {
    @include text-styles(
      $colorText,
      $article-link-text-size,
      $line-height-medium,
      $letter-spacing-extra-small
    );
    text-transform: capitalize;
  }

  &:hover {
    background-color: $highlightColor;
    transform: scale($scale-factor-small);
  }

  &_text {
    @include text-styles(
      $colorText,
      $font-size-text,
      $line-height-medium,
      $letter-spacing-small
    );
    padding-bottom: 40.5px;
  }
  &__button {
    &.second-article-button svg circle {
      fill: $buttonTextColor;
    }
  }
}
</style>
