<template>
  <article class="article article__full">
    <h3 class="article__title article__full_title">{{ article.title }}</h3>
    <component
      v-for="(content, index) in article.contents"
      :key="index"
      :is="content.type"
      v-bind="getProps(content.type, content.data)"
    />
  </article>
</template>

<script>
import { mapGetters } from "vuex";
import ArticleText from "./ArticleText.vue";
import ArticleList from "./ArticleList.vue";
import ArticleImage from "./ArticleImage.vue";
import ArticleQuote from "./ArticleQuote.vue";

export default {
  name: "FullArticle",
  props: {
    article: {
      type: Object,
      required: true,
    },
    imgClass: {
      type: String,
    },
  },
  components: { ArticleText, ArticleList, ArticleImage, ArticleQuote },
  data() {
    return {};
  },
  methods: {
    getProps(type, data) {
      if (type === "ArticleImage") {
        return { ...data, imgClass: this.imgClass };
      }
      return data;
    },
  },
  computed: {
    ...mapGetters(["articles"]),
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/styles/variables.scss";
@import "../assets/styles/mixins.scss";
.article__full {
  width: 100%;
  border: none;
  padding: 0;
  border-radius: 0;
  padding-bottom: 27px;
  &:hover {
    background-color: initial;
    transform: initial;
  }
  &_image-link {
    margin-bottom: 45px;
  }
  &_some {
    margin-bottom: 48px;
  }
  &_title {
    @include text-styles(
      $colorHeading,
      $h2-size,
      $line-height-small,
      $letter-spacing-large
    );
    font-family: $textHeading;
    padding-bottom: 12px;
    margin-bottom: 30px;
  }
  &:nth-child(2) {
    background-color: initial;
  }

  &:nth-child(2) svg {
    fill: initial;
  }
}
.details {
  padding-top: 0;
  padding-bottom: 0;
  max-width: 66.5%;
}
.blog-details {
  padding-top: 200px;
  padding-bottom: 96px;
  &__flex {
    display: flex;
    column-gap: 57px;
  }
}
</style>
